
import React ,{ useState ,useEffect} from "react";
import { MapContainer, TileLayer, Marker, Popup,useMapEvents,useMap, ImageOverlay,Polyline ,SVGOverlay} from "react-leaflet";
import L from 'leaflet'; // user for import leaflet
import "leaflet-control-geocoder/dist/Control.Geocoder.css";
import "leaflet-control-geocoder/dist/Control.Geocoder";
// import osm from "./osm_provider";



delete L.Icon.Default.prototype._getIconUrl;

// use default marker icon 
L.Icon.Default.mergeOptions({
    iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
    iconUrl: require('leaflet/dist/images/marker-icon.png'),
    shadowUrl: require('leaflet/dist/images/marker-shadow.png')
});


// get user current location
function Location(){
 const [position, setPosition] = useState(null)
    const map = useMapEvents({      // useMapEvents- Hook attaching the provided LeafletEventHandlerFnMap event handlers to the map instance and returning the instance in any descendant of a MapContainer.
      click() {                     // Interaction events. click()- Fired when the user clicks (or taps) the map.
        map.locate()                //Geolocation methods.locate()- Tries to locate the user using the Geolocation API, firing a locationfound event with location data on success or a locationerror event on failure, and optionally sets the map view to the user's location with respect to detection accuracy (or to the world view if geolocation failed).
      },
      locationfound(e) {                    //Location events. locationfound- Fired when geolocation (using the locate method) went successfully.
        setPosition(e.latlng)
        map.flyTo(e.latlng, map.getZoom())  //Methods for modifying map state. flyTo- Sets the view of the map (geographical center and zoom) performing a smooth pan-zoom animation.
      },
    })
  
    return position === null ? null : (
//L.Marker is used to display clickable/draggable icons on the map. Extends Layer.

      <Marker riseOnHover={true}   position={position}>     
        <Popup>I'm are here</Popup>
      
      </Marker>     
    )       
  }
  
function Test({ location, search }) {
  const map = useMap();
  if (location) map.setView(location);

  return location ? (
    <Marker
      draggable
      position={location}
    
     
    >
      <Popup>you are here {search}</Popup>
    </Marker>
  ) : null;
}


function MyMap(){
   
const polyline = [
    [22.7679,78.000],
    [23.7679,79.8718],
  ]
//   const polygon = [
//     [22.7679,78.000],
//     [24.7679,80.8718],
//     [25.52, 81.00],
//   ]
//   const rectangle = [
//     [22.7679,78.000],
//     [23.7679,79.8718]
//   ]
  const bounds =[ 
    [22.7679,78.000],
    [23.7679,79.8718]
  ]
  const bound =[ 
    [24.7679,80.8718],
 [28.52, 85.00],
  ]
  
  //search location 
  const [loc, updLoc] = useState();
  const [search, updSearch] = useState();

  useEffect(() => {
    const geocoder = L.Control.Geocoder.nominatim();
    if (search)
      geocoder.geocode(search, (results) => {
        //console.log(results);
        var r = results[0];
        if (r) {
          const { lat, lng } = r?.center;
          updLoc({ lat, lng });
          //console.log(r);
        }
      });
  }, [search]);


return(
  
    
 <>
  {/* {loc?.lat},{loc?.lng} */} 
  <input 
       placeholder="search here"
       onChange={(e) => updSearch(e.target.value)}
     /> 
      
    <MapContainer                 //The central class of the API  it is used to create a map on a page and manipulate it.
    center={[21.7679,78.8718]}   //Map State Options. Initial geographic center of the map
     zoom={10}                   //	Map state change events. Initial map zoom level
     style={{ height: "95vh"}}

>
  <TileLayer            //Used to load and display tile layers on the map. Note that most tile servers require attribution, which you can set under Layer. Extends GridLayer.
      attribution='&copy; <a href="https://www.maptiler.com/>&copy;MapTiler</a> <a href="https://www.openstreetmap.org/>&copy; OpenStreetMap contributors</a>'
      url="https://api.maptiler.com/maps/basic/256/{z}/{x}/{y}.png?key=vxPhavHdPXLyI37krAWx"
    />
       <SVGOverlay attributes={{ stroke: 'red' }} bounds={bound}>
      <rect x="0" y="0" width="100%" height="100%" fill="pink" />
      <circle r="5" cx="10" cy="10" fill="red" />
      <text x="20%" y="50%" stroke="white">
        welcome to my world
      </text>
    </SVGOverlay>
    
    <Location />
    {/* <Polygon pathOptions={{ color: 'purple' }} positions={polygon} />  */}
    <Polyline pathOptions={{ color: 'red' }} positions={polyline} />
    {/* <Circle center={[22.7679,78.000]} radius={200} pathOptions={{ color: 'blue' }} />
    <Rectangle bounds={rectangle} pathOptions={{ color: 'black' }} />  */}
    

     <ImageOverlay //this function puts image specified in url in given bounds coordinates //Used to load and display a single image over specific bounds of the map. Extends Layer.
      url="http://www.lib.utexas.edu/maps/historical/newark_nj_1922.jpg"
      bounds={bounds}
      opacity={0.5}
      zIndex={10}   />    
      <Test location={loc} search={search} />
    </MapContainer>
    
  
    </>
)
}
export default MyMap;
